    methods:
    {
        search(keyword)
        {
            console.log(keyword);
            var pre_allsite=this.st_allsite;
            var pre_book=this.st_book;
            var pre_movie=this.st_movie;
            var i;
            this.allsite=[];
            this.book=[];
            this.movie=[];
            for(i=0;i<pre_allsite.length;i++){
                if(pre_allsite[i].name.includes(keyword)){
                    this.allsite.push(pre_allsite[i]);
                }
            }
            for(i=0;i<pre_book.length;i++){
                if(pre_book[i].name.includes(keyword)){
                    this.book.push(pre_book[i]);
                }
            }
            for(i=0;i<pre_movie.length;i++){
                if(pre_movie[i].name.includes(keyword)){
                    this.movie.push(pre_movie[i]);
                }
            }
        }
    },
    created() {
        this.st_allsite=this.allsite;
        this.st_book=this.book;
        this.st_movie=this.movie;
        var guro=localStorage.getItem('username');
        var gurt=sessionStorage.getItem('username');
        if(guro!=null) this.urid=guro;
        if(gurt!=null) this.urid=gurt;
        console.log(guro);
        console.log(gurt);
        console.log(this.urid);
    }
